.profile__name {
  margin: 0;
  font-size: 42px;
  line-height: 48px;
  font-weight: 500;

  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@media screen and (max-width:700px){
  .profile__name {
    font-size: 27px;
    line-height: 33px;
    text-align: center;
    
  }
}